import React, { Fragment } from 'react'
import styles from './ButtonPopup.module.css'
import buttonStyles from '../Button.module.css'

import {
  Button,
  IconButton,
  Typography,
  DialogContent,
  Tooltip,
  DialogTitle,
  Dialog,
  Grow,
  AppBar,
  Toolbar
} from '@mui/material'

import { Icon } from '../../Icon'

import AbstractArea from '../../../AbstractArea'
import Area from '../../../Area'
import CloseIcon from '@mui/icons-material/Close'
import { DEFAULT_WIDGET_CONFIG } from "../../../default-widget-config"
import { connect } from 'react-redux'

const DIALOG_MIN_MARGIN = "64px"
const DEFAULT_BUTTON_POPUP_CONFIG = DEFAULT_WIDGET_CONFIG["BB ButtonPopup"]

class ButtonPopup extends AbstractArea {
  state = { openDialog: false, areas: [] }

  Transition = React.forwardRef(function Transition(props, ref) {
    return <Grow direction="up" ref={ref} {...props} unmountOnExit />
  })

  handleButtonClick = () => {
    let updatepopup = Math.random() * 1000033 + 'f'

    this.props.publish('updatepopup', updatepopup)
    this.setState({ openDialog: true })
  }

  handleCloseDialog = () => {
    this.setState({ openDialog: false })
  }

  

  componentDidMount() {
    if (this.props.openDialog) {
      let updatepopup = Math.random() * 1000033 + 's'
      this.props.publish('updatepopup', updatepopup)
      this.setState({ openDialog: true })
    }
  }

  shouldComponentUpdate(props, p) {
    if (this.state.openDialog !== p.openDialog) return true

    if (this.state.openDialog) {
      if (this.props.mode === 'edit') return true
      if (this.props.closeAtParameterChange != null) {
        let parameter = this.props.closeAtParameterChange
        let nprop = props.pubsub[parameter]
        let value = this.props.pubsub[parameter]

        if (value !== nprop) {
          this.setState({ openDialog: false })
        }
      }
    }
    else
    {
      if (this.props.autoOpenOnParameter) 
      {
       
        var parameter = this.props.autoOpenOnParameter;
         var timestamp=null;
         try {timestamp=window.top.changes[parameter+"Timestamp"]}catch(e){};
        if ( (props.pubsub[parameter]!=null) && (props.pubsub[parameter]!=""))
        {
          if (this.props.pubsub[parameter]!=props.pubsub[parameter])
          {
            if  ( (props.pubsub[parameter]!="http://www.buildingbits.nl/reset") && (props.pubsub[parameter]!=""))
            {
             // console.log("open dialog ",this.props.pubsub[parameter],props.pubsub[parameter]);
              this.setState({ openDialog: true,timestamp:timestamp })
            }
            else
            {
              //console.log("fix auto open popup");
            }
          }
          else
          {
            // parameter is the same is it available in the changes log?
             if (window.top.changes[parameter]!=null)
             {
              // must be reisnerted ?
              if (this.state.timestamp!=window.top.changes[parameter+"Timestamp"])
              {
                if ((props.pubsub[parameter]!="http://www.buildingbits.nl/reset") && (props.pubsub[parameter]!="") )
                {
                   this.setState({ openDialog: true,timestamp:timestamp });
                  // console.log("open dialog timestamp ",this.props.pubsub[parameter],props.pubsub[parameter]);
                }
                else
                {
                 // console.log("fix auto open popup 2");
                }
              }
             }
          }
        }
      }
    }



    return true
  }

  render() {
    let title = this.props.title
    if (title == null) {
      title = ''
    }
    let tooltip = this.props.tooltip
    if (tooltip == null) {
      tooltip = ''
    }
    let popupTitle = this.props.popupTitle
    if (popupTitle == null) {
      popupTitle = ''
    }
    let disable=false;
    if (this.props.disable)
    {
      var disableParameter = this.props.disable;
      var dvalue=this.props.pubsub[disableParameter];
     // console.log(disableParameter,dvalue);
      if ((dvalue==null) || (dvalue==true)|| (dvalue=="true")  )
      {
        
        disable=true;
       // console.log(disable);
      }
      if  ( (dvalue=="http://www.buildingbits.nl/reset") )
      {
        disable=true;
      }
     
    }
    var inputProps={};

    

    let beginIcon = this.props.beginIcon
    let endIcon = this.props.endIcon
    let openDialog = this.state.openDialog
    let fullScreen = this.state.popupFullScreen
    if (fullScreen == null) fullScreen = false
    
    const fontSize = this.props.buttonFontSize ? this.props.buttonFontSize : 'inherit'
    const fontFamily = this.props.buttonFontFamily ? this.props.buttonFontFamily : 'inherit'


    let areaConfig = {}
    try {
      areaConfig = this.props.areas[0]
    } catch (e) {}

    const userMinWidth = this.props.popupMinWidth || DEFAULT_BUTTON_POPUP_CONFIG.popupMinWidth
    const userMaxWidth = this.props.popupMaxWidth || DEFAULT_BUTTON_POPUP_CONFIG.popupMaxWidth
    const userMinHeight = this.props.popupMinHeight || DEFAULT_BUTTON_POPUP_CONFIG.popupMinHeight
    const userMaxHeight = this.props.popupMaxHeight || DEFAULT_BUTTON_POPUP_CONFIG.popupMaxHeight


    return (
      <Fragment>
        <Tooltip title={tooltip}>
          <Button
            className={`${styles.openButton} ${styles.button1} ${buttonStyles.button}`}
            color="grey"
            size="small"
            endIcon={endIcon && <Icon icon={endIcon} />}
            startIcon={beginIcon && <Icon icon={beginIcon} />}
            onClick={() => this.handleButtonClick()}
            variant="contained"
            disabled={disable}
            data-custom-styling={Boolean(this.props.customStyling)}
            data-has-button-shadow={Boolean(this.props.hasButtonShadow)}
            style={{
              '--button-border-radius': this.props.buttonBorderRadius,
              '--text-color': this.props.textColor,
              '--background-color': this.props.bgColor,
              '--button-font-size': fontSize,
              '--button-font-family': fontFamily,
            }}
          >
            {title}
          </Button>
        </Tooltip>

        <Dialog
          fullScreen={fullScreen}
          sx={this.props.appCss}
          PaperProps={{
            sx: {
              minWidth: `min(${userMinWidth}, calc(100vw - ${DIALOG_MIN_MARGIN}))`,
              maxWidth: `min(${userMaxWidth}, calc(100vw - ${DIALOG_MIN_MARGIN}))`,
              minHeight: `min(${userMinHeight}, calc(100vh - ${DIALOG_MIN_MARGIN}))`,
              maxHeight: `min(${userMaxHeight}, calc(100vh - ${DIALOG_MIN_MARGIN}))`
            }
          }}

          open={openDialog}
         {...inputProps}
          
          TransitionComponent={this.Transition}
          onClose={(e, reason) => {
            if (reason === "backdropClick" && this.props.popupCloseOnBackdropClick
              || reason === "escapeKeyDown" && this.props.popupCloseOnEscapeKey) {
              this.handleCloseDialog()
            }
          }}
          aria-labelledby="iets"
        >
          {fullScreen ? (
            <Fragment>
              <AppBar className={styles.appBar}>
                <Toolbar>
                  <IconButton
                    edge="start"
                    color="inherit"
                    onClick={this.handleCloseDialog}
                    aria-label="close"
                  >
                    <CloseIcon />
                  </IconButton>
                  <Typography variant="h6" className={styles.title}>
                    {popupTitle}
                  </Typography>
                </Toolbar>
              </AppBar>
              <br></br>
              <br></br>
              <br></br>
              <Area {...this.props} />
            </Fragment>
          ) : (
            <Fragment>
              <DialogTitle className={styles.dialogTitle}>
                <div>{popupTitle}</div>
                <IconButton
                  className={styles.closeButton}
                  onClick={this.handleCloseDialog}
                >
                  {' '}
                  <CloseIcon />{' '}
                </IconButton>
              </DialogTitle>
              <DialogContent dividers style={{
                padding: this.props.popupContentPadding ?? undefined
              }}>
                <Area
                  {...this.props}
                  config={areaConfig}
                  index={0}
                  onChange={this.props.onChange}
                  closeDialog={this.handleCloseDialog}
                />
              </DialogContent>
            </Fragment>
          )}
        </Dialog>
      </Fragment>
    )
  }
}

const mapStateToProps = (state) => ({
  appCss: state.currentViewer.appCss,
})

export default connect(mapStateToProps)(ButtonPopup)
